import React, {Component} from 'react';
// CSS
import './reset.css';
import 'normalize.css';
import './App.css';
// Component
import TodoInput from './TodoInput';
import TodoItem from './TodoItem';
import UserDialog from './UserDialog';
import AV, {getCurrentUser, signOut} from "./leanCloud"

// Component App
class App extends Component {
    constructor(props) {
        super(props)
        // localStorage
        this.state = {
            user: getCurrentUser() || {},
            newTodo: '',
            todoList: []
        }
        //  ==> 
        this.checkLoginStatus();
    }

    render() {
        console.log("App render");

        // todos  TodoItem
        let todos = this.state.todoList
        // Todo
            .filter((item) => !item.deleted)
            // Todo
            .map((item, index) => {
                return (
                    // TodoItemtodos
                    <li key={index}>
                        <TodoItem todo={item}
                                  onToggle={this.toggle.bind(this)}
                                  onDelete={this.delete.bind(this)}/>
                    </li>
                )
            })
        // 
        return (
            <div className="App">
                <h1>
                    {this.state.user.username || ""}
                    {this.state.user.id ?
                        <button id="signOut" onClick={this.signOut.bind(this)}></button> :
                        null}
                </h1>
                <div className="inputWrapper">
                    {/*
                     ** content  newTodo
                     ** onSubmit  addTodo
                     ** onChange  changeTitle
                     **/}
                    <TodoInput content={this.state.newTodo}
                               onSubmit={this.addTodo.bind(this)}
                               onChange={this.changeTitle.bind(this)}/>
                </div>
                {/*  Todos */}
                <ol className='todoList'>
                    {todos}
                </ol>
                {this.state.user.id ?
                    null :
                    <UserDialog onSignUp={this.onSignUpOrSignIn.bind(this)}
                                onSignIn={this.onSignUpOrSignIn.bind(this)}/>}
            </div>
        );
    }

    //  ==> 
    checkLoginStatus() {
        if (this.state.user !== {}) {
            this.searchTodo()
        }
    }

    // 
    // https://leancloud.cn/docs/leanstorage_guide-js.html#
    saveTodo() {
        // 
        var AVTodos = AV.Object.extend('Todo');
        var avTodos = new AVTodos();
        // 
        var acl = new AV.ACL();
        acl.setReadAccess(AV.User.current(), true)
        acl.setWriteAccess(AV.User.current(), true)
        // 
        let dataString = JSON.stringify(this.state.todoList)
        avTodos.set('content', dataString);
        avTodos.setACL(acl)
        // leanCloud
        avTodos.save().then((todo) => {
            let stateCopy = JSON.parse(JSON.stringify(this.state))
            //  id ==> 
            stateCopy.todoList.id = todo.id
            this.setState(stateCopy)
            console.log('');
        }, function (error) {
            alert('')
        })
    }

    // 
    // https://leancloud.cn/docs/leanstorage_guide-js.html#
    updateTodo() {
        let dataString = JSON.stringify(this.state.todoList)
        let avTodos = AV.Object.createWithoutData('Todo', this.state.todoList.id)
        avTodos.set('content', dataString)
        avTodos.save().then(function (e) {
            console.log(e)
            console.log('update success')
        })
    }

    //  leanCloud id
    // reference ==> https://leancloud.cn/docs/leanstorage_guide-js.html#
    searchTodo() {
        if (this.state.user) {
            var query = new AV.Query('Todo');
            query.find().then((todos) => {
                // console.log(todos)
                let avAlltodos = todos[0]
                let id = avAlltodos.id
                let stateCopy = JSON.parse(JSON.stringify(this.state))
                stateCopy.todoList = JSON.parse(avAlltodos.attributes.content)
                stateCopy.todoList.id = id
                this.setState(stateCopy)
            }, function (error) {
                console.error(error)
            })
        }
    }

    // 
    changeTodo() {
        this.state.todoList.id ? this.updateTodo() : this.saveTodo()
    }


    // /
    onSignUpOrSignIn(user) {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.user = user;
        this.setState(stateCopy);
        this.searchTodo();
    }


    //  ===> 
    signOut() {
        let confirmSignOut = window.confirm("")
        if (confirmSignOut) {
            signOut()
            let stateCopy = JSON.parse(JSON.stringify(this.state));
            stateCopy.user = {};
            stateCopy.todoList = [];
            this.setState(stateCopy)
        }
    }

    // componentDidUpdate 
    componentDidUpdate() {
    }

    //  TodoItem
    delete(event, todo) {
        todo.deleted = true;
        this.setState(this.state);
        this.changeTodo();
    }

    //  TodoItem 
    toggle(e, todo) {
        todo.status = todo.status === 'completed' ? '' : 'completed';
        this.setState(this.state);
        this.changeTodo();
    }

    // TotoInput
    changeTitle(event) {
        this.setState({
            newTodo: event.target.value,
            todoList: this.state.todoList
        })
    }

    // TodoListTodo
    addTodo(event) {
        this.state.todoList.push({
            id: idMaker(),
            title: event.target.value,
            status: "",
            deleted: false
        })
        this.setState({
            newTodo: '',
            todoList: this.state.todoList
        })
        this.changeTodo();
    }
}

//  ID
let id = 0;
function idMaker() {
    id += 1;
    return id;
}

// 
export default App;





