import React, {Component} from "react";
// CSS
import "./UserDialog.css"
// leanCloud
import {signUp, signIn, sendPasswordResetEmail} from "./leanCloud"

// Component UserDialog
export default class UserDialog extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selected: "signIn",
            selectedTab: "signInOrSignUp",
            formData: {
                email: '',
                username: '',
                password: ''
            }
        }
    }

    //  /
    switch(e) {
        this.setState({
            selected: e.target.value
        });
        this.changeStyle(e)
    }

    // 
    signUp(e) {
        e.preventDefault();
        let {email, username, password} = this.state.formData;
        let success = (user) => {
            this.props.onSignUp.call(null, user);
            console.log(user)
        };
        let error = (error) => {
            console.log(error.code);
            switch (error.code) {
                case 125:
                    alert("");
                    break;
                case 200:
                    alert("");
                    break;
                case 201:
                    alert("");
                    break;
                case 202:
                    alert("");
                    break;
                case 217:
                    alert("");
                    break;
                case 218:
                    alert("");
                    break;
                default:
                    console.log(error);
                    alert("");
                    break;
            }
        };
        signUp(email, username, password, success, error)
    }

    // 
    signIn(e) {
        e.preventDefault();
        let {username, password} = this.state.formData;
        let success = (user) => {
            this.props.onSignIn.call(null, user)
        };
        let error = (error) => {
            // console.log(error.code);
            switch (error.code) {
                case 200:
                    alert("");
                    break;
                case 201:
                    alert("");
                    break;
                case 210:
                    alert("");
                    break;
                case 211:
                    alert("");
                    break;
                case 217:
                    alert("");
                    break;
                case 218:
                    alert("");
                    break;
                case 219:
                    alert("15");
                    break;

                default:
                    console.log(error);
                    alert("");
                    break;
            }

        };
        signIn(username, password, success, error)
    }

    changeFormData(key, e) {
        // this.stateformData
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.formData[key] = e.target.value;
        this.setState(stateCopy);
    }

    //  / 
    changeStyle(e) {
        e.target.parentNode.style.boxShadow = "none";
        e.target.parentNode.style.background = "RGB(247, 247, 247)";
        e.target.parentNode.parentNode.childNodes.forEach((lab) => {
            if (lab.nodeType === 1 && lab !== e.target.parentNode) {
                lab.style.boxShadow = "inset 0px 0px 1px 0px rgba(0, 0, 0, .2)";
                lab.style.background = "rgba(222, 222, 222, .6)";
            }
        })
    }

    render() {
        // {/*  */}
        let signUpForm = (
            <form className="signUp"
                  onSubmit={this.signUp.bind(this)}>
                <div className="row">
                    <label></label>
                    <input type="email"
                           placeholder="Email Address"
                           value={this.state.formData.email}
                           onChange={this.changeFormData.bind(this, "email")}/>
                </div>
                <div className="row">
                    <label></label>
                    <input type="text"
                           placeholder="username"
                           value={this.state.formData.username}
                           onChange={this.changeFormData.bind(this, "username")}/>
                </div>
                <div className="row">
                    <label></label>
                    <input type="password"
                           placeholder="password"
                           value={this.state.formData.password}
                           onChange={this.changeFormData.bind(this, "password")}/>
                </div>
                <div className="row action">
                    <button type="submit"></button>
                </div>
            </form>
        );
        // {/*  */}
        let signInForm = (
            <form className="signIn"
                  onSubmit={this.signIn.bind(this)}>
                <div className="row">
                    <label htmlFor=""></label>
                    <input type="text"
                           placeholder="username"
                           value={this.state.formData.username}
                           onChange={this.changeFormData.bind(this, "username")}/>
                </div>
                <div className="row">
                    <label htmlFor=""></label>
                    <input type="password"
                           placeholder="password"
                           value={this.state.formData.password}
                           onChange={this.changeFormData.bind(this, "password")}/>
                </div>
                <div className="row action">
                    <button type="submit"></button>
                    <a href="javascript:" onClick={this.showForgetPassword.bind(this)}></a>
                </div>
            </form>
        );

        let signInOrSignUp = (
            <div className="signInOrSignUp">
                <nav>
                    <label htmlFor="signUp">
                        <input type="radio"
                               value="signUp"
                               id="signUp"
                               checked={this.state.selected === "signUp"}
                               onChange={this.switch.bind(this)}/>
                    </label>
                    <label htmlFor="signIn">
                        <input type="radio"
                               value="signIn"
                               id="signIn"
                               checked={this.state.selected === "signIn"}
                               onChange={this.switch.bind(this)}/>
                    </label>
                </nav>
                <div className="panes">
                    {this.state.selected === "signUp" ? signUpForm : null}
                    {this.state.selected === "signIn" ? signInForm : null}
                </div>
            </div>
        );

        let forgetPassword = (
            <div className="forgetPassword">
                <h3></h3>
                <form action=""
                      className="forgetPassword"
                      onSubmit={this.resetPassword.bind(this)}>
                    <div className="row">
                        <label htmlFor=""></label>
                        <input type="email"
                               placeholder="Email Address"
                               value={this.state.formData.email}
                               onChange={this.changeFormData.bind(this, "email")}/>
                    </div>
                    <div className="row action">
                        <button type="submit"></button>
                        <a href="javascript:;" onClick={this.returnToSignIn.bind(this)}></a>
                    </div>
                </form>
            </div>
        );

        return (
            <div className="UserDialog-Wrapper">
                <div className="UserDialog">
                    {/*  /*/}
                    {this.state.selectedTab === "signInOrSignUp" ? signInOrSignUp : forgetPassword}
                </div>
            </div>
        )
    }

    // 
    showForgetPassword() {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.selectedTab = "forgetPassword";
        this.setState(stateCopy);
    }

    resetPassword(e) {
        e.preventDefault();
        sendPasswordResetEmail(this.state.formData.email);
        alert("")
    }

    // 
    returnToSignIn() {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.selectedTab = "signInOrSignUp";
        this.setState(stateCopy);
    }
}



